import { useReducedMotion } from "framer-motion";
import {
  darkModeLocalStorageKey,
  getLocalStorageValue,
  setLocalStorageValue,
  useResponsive,
} from "@faharmony/helpers";
import { IThemeProps } from "./types";

/** Get dimensions of Text, when rendered on DOM. */
export function getTextDimensions(
  value: string,
  type: Exclude<keyof IThemeProps["text"], "font"> = "p"
) {
  const container = document.createElement(type);
  document.body.appendChild(container);
  container.style.height = "auto";
  container.style.width = "auto";
  container.style.position = "absolute";
  container.style.whiteSpace = "no-wrap";
  container.innerHTML = value;
  const width = Math.ceil(container.clientWidth);
  const height = Math.ceil(container.clientHeight);
  document.body.removeChild(container);
  return { width, height };
}

/** Get the current dark-mode preference. */
export const getCurrentDarkModePreference = (): boolean => {
  let darkMode = false;
  // Check local storage
  if (getLocalStorageValue(darkModeLocalStorageKey)) {
    darkMode = getLocalStorageValue(darkModeLocalStorageKey) === "true";
  } else {
    // Check CSS color preference
    darkMode =
      window.matchMedia &&
      window.matchMedia("(prefers-color-scheme: dark)").matches;
  }
  return darkMode;
};

/** Set the current dark-mode preference. */
export const setCurrentDarkModePreference = (darkMode: boolean) =>
  setLocalStorageValue(darkModeLocalStorageKey, darkMode.toString());

/** Hook to check is animation should be disabled or not. */
export const useDisableAnimation = (): boolean => {
  const shouldReduceMotion = useReducedMotion();
  const { isDesktop } = useResponsive();
  return !isDesktop || shouldReduceMotion;
};
