const CalendarOverlay = ({ children, classNames, ...props }: any) => (
  <StyledCalendar {...props} className={classNames.overlayWrapper}>
    {children}
  </StyledCalendar>
);

const NavBar = ({
  onPreviousClick,
  onNextClick,
  month,
  localeUtils,
  previousMonth,
  nextMonth,
  showNextButton,
  showPreviousButton,
}: NavbarElementProps) => {
  const currentMonth = localeUtils.getMonths()[month.getMonth()];
  const currentYear = month.getFullYear().toString();
  const months = localeUtils.getMonths();
  const prev = months[previousMonth.getMonth()];
  const next = months[nextMonth.getMonth()];
  return (
    <Box justifyContent="space-between" className="Calendar-Navigation">
      {showPreviousButton && (
        <Button
          icon={faAngleLeft}
          onClick={() => onPreviousClick()}
          spacing="xs"
          tooltip={prev}
        />
      )}
      <Text
        value={currentMonth + " " + currentYear}
        variant="h5"
        align="center"
      />
      {showNextButton && (
        <Button
          icon={faAngleRight}
          onClick={() => onNextClick()}
          spacing="xs"
          tooltip={next}
        />
      )}
    </Box>
  );
};

