/**
 * DatePicker
 * ---
 * Description of component
 * @jira https://fasolutions.jira.com/browse/HAR-74
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @copyright FA Solutions Oy
 * @version 1.6.0
 * @example <DatePicker />
 */
const DatePicker = (props: IDatePickerProps): JSX.Element => {
  const { name, rules, render, onFocus, defaultValue, ...rest } = props;
  // get control
  let formMethods: UseFormMethods | undefined = undefined;
  try {
    formMethods = useFormContext();
  } catch {}

  // Render
  return formMethods ? (
    <Controller
      as={
        <_PickerInput
          {...rest}
          onDayChange={(date) =>
            formMethods && formMethods.setValue("date", date)
          }
        />
      }
      control={formMethods.control}
      name={name || "date"}
      defaultValue={defaultValue}
      rules={rules}
      render={render}
      onFocus={onFocus}
    />
  ) : (
    <_PickerInput {...rest} />
  );
};

export { DatePicker, IDatePickerProps };
